@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
body {
  background-color: #000;
  color: #fafafa;
  margin: 20px;
  font: 32px 'Indie Flower', cursive;
  font-weight: bold;
  height: 100vh;
  padding: 0;
}

.square {
  background-color: #444;
  color: #fafafa;
  border: 1px solid #999;
  padding: 0;
  font-size: 70px;
  text-align: top;
  width: 100px;
  height: 100px;
  font-family: 'Indie Flower', cursive;
}

.square:focus {
  outline: none;
  background: #9b1103;
}

/* xxs - Double extra small devices (portrait phones, less than 576px) */
@media (max-width: 349.99px) {
  .square {
    width: 80px;
    height: 80px;
    font-size: 55px;
  }
}

.board-row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
}

.status {
  margin: 20px;
  font-size: 24px;
}

.title {
  font-size: 4rem;
  color: #9b1103;
  -webkit-margin-before: 4rem;
          margin-block-start: 4rem;
  -webkit-margin-after: 6rem;
          margin-block-end: 6rem;
  text-transform: capitalize;
}

/* xxs - Double extra small devices (portrait phones, less than 576px) */
@media (max-width: 349.99px) {
  .title {
    -webkit-margin-before: 3rem;
            margin-block-start: 3rem;
    -webkit-margin-after: 4rem;
            margin-block-end: 4rem;
    font-size: 42px;
  }
}

/* xs - Extra small devices (portrait phones, less than 576px) */
@media (min-width: 350px) and (max-width: 575.99px) {
  .title {
    -webkit-margin-before: 3rem;
            margin-block-start: 3rem;
    -webkit-margin-after: 4rem;
            margin-block-end: 4rem;
    font-size: 42px;
  }
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

/* credit: https://coder-coder.com/display-divs-side-by-side/ */
.game-container {
  display: -webkit-flex;
  display: flex;
}

.game-child {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.game-child:first-child {
    margin-right: 5rem;
}

button,
::marker {
  font: 18px 'Indie Flower', cursive;
  font-weight: bold;
}

/* xxs - Double extra small devices (portrait phones, less than 576px) */
@media (max-width: 349.99px) {
  .game-container {
    display: block;
  }

  .game-child:first-child {
      margin-right: 0;
  }

  button,
  ::marker {
    font: 18px 'Indie Flower', cursive;
    font-weight: bold;
  }

  .game-info {
    margin-left: 1rem;
    margin-bottom: 5rem;
  }

  .status {
    margin-top: 2rem;
    margin-bottom: 0;
  }

  .moves {
    margin-top: 1rem;
  }
}

/* xs - Extra small devices (portrait phones, less than 576px) */
@media (min-width: 350px) and (max-width: 575.99px) {
  .game-container {
    display: block;
  }

  .game-child:first-child {
      margin-right: 0;
  }

  button,
  ::marker {
    font: 18px 'Indie Flower', cursive;
    font-weight: bold;
  }

  .game-info {
    margin-left: 3rem;
    margin-bottom: 5rem;
  }

  .status {
    margin-top: 2rem;
    margin-bottom: 0;
  }

  .moves {
    margin-top: 1rem;
  }
}

/* sm - Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .game-child:first-child {
      margin-right: 2rem;
  }
}

.footer{
  background-color: #222;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 12px;
  font-family: 'Lato', sans-serif;
}

.icon {
  margin: 0 0.3rem 0 0.3rem;
}

.github {
  color: #fafafa;
}

.github:hover {
  color: #9b1103;
}

